.pricing {
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include notebook {
    margin: 50px auto;;
  }

&__boxes {
  display: flex;
  align-items: center;
  justify-content: center;
  @include notebook {
    width: 100%;
    max-width: 370px;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 50px;
    justify-content: center;
    padding: 25px 10px 0 10px;
  
    &:last-child {
      margin-bottom: 0;
    }
  }

&__box {
  width: 370px;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid var(--accent-color);
}

&__details {
  margin-top: 34px;
  margin-bottom: 60px;
  text-align: center;
  @include notebook {
    width: 50%;
    margin: 0;
  }
}

&__type {
  font-size: 2.25rem;
  font-family: $font-raleway;
  font-weight: 700;
}

&__price {
  font-size: 2.875rem;
  font-family: $font-open-sans;
  font-weight: 300;
  margin-top: 25px;
  color: var(--accent-color);
}

&__limited {
  text-align: center;
  margin-top: 15px;
  font-size: 0.75rem;
  font-family: $font-raleway;
  font-weight: 1000;
}

&__items {
  text-align: center;
}

&__item {
  font-size: 14px;
  margin-bottom: 20px;
}

&__item::before {
  content: "";
  width: 11px;
  height: 11px;
  display: inline-block;
  margin-right: 10px;
  background-image: url("/assets/Checkmark.svg");
}

&__item--not::before {
  background-image: url("/assets/Close.svg");
}

&__box .btn {
  position: relative;
  bottom: -25px;
  font-size: 0.8125rem;
  background-color: var(--accent-color);
}

/*** Basic Box ***/
&__box--basic {
 --accent-color: #{$color-redish};
}

&__box--basic &__price {
  margin-bottom: 30px;
}


/*** Professional Box **/
&__box--professional {
  --accent-color: #{$color-primary};
  width: 400px;
  margin: 0 40px;
  box-shadow: 5px 1px 27px $color-primary-shadow;
}

&__box--professional &__type {
  font-size: 1.3125rem;
}

&__box--professional &__item {
  margin-bottom: 25px;
}

/*** Premium Box ***/
&__box--premium {
  --accent-color: #{$color-green};
}
}}

